html {
    width: 100%;
    height: 100%;
    min-width: 300px;
}

body {
    font-family: Helvetica Neue;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 18px;
    color: #000;
    width: 100%;
    height: 100%;
    min-height: 100%;
    overflow-x: hidden;
    min-width: 300px;

    #root,
    .App {
        min-height: 100%;
        height: 100%;
        width: 100%;
    }
    .App {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
}

a {
    color: #000;
    text-decoration: none;

    &:hover {
        color: $main_color;
    }
}

h1,
h2,
h3,
h4,
h5,
h6,
ul {
    margin: 0;
    padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
    font-weight: normal;
    line-height: 30px;

    & + h1,
    & + h2,
    & + h3,
    & + h4,
    & + h5,
    & + h6,
    & + .h1,
    & + .h2,
    & + .h3,
    & + .h4,
    & + .h5,
    & + .h6 {
        margin-top: 15px;
    }
}

h1,
.h1 {
    font-size: 24px;
}
h2,
.h2 {
    font-size: 20px;
}
h3,
.h3 {
    font-size: 18px;
}
h4,
.h4 {
    font-size: 17px;
}
h5,
.h5 {
    font-size: 16px;
}
h6,
.h6 {
    font-size: 15px;
}

.container {
    // @include container();
    // @include sm(max-width, rem(510));
    // @include md(max-width, rem(690));
    // @include lg(max-width, rem(930));
    // @include xl(max-width, rem(1110));

    padding: 0 15px;
    max-width: 1110px;
    margin: 0 auto;
}

main.content {
    padding-top: 92px;
}

.page-content {
    margin-top: 11px;
    ul {
        padding-left: 40px;
        list-style: none;

        li::before {
            content: "•";
            color: $main_color;
            display: inline-block;
            width: 1em;
            margin-left: -1em;
        }
    }
}

.two-rows {
    display: flex;
    flex-direction: column;

    @media screen and (min-width: 1140px) {
        flex-direction: row;
    }
}

body {
    header,
    main,
    footer {
        transition: transform 0.5s;
    }
}
.side-open {
    pointer-events: none;
    overflow: hidden;
    header,
    main,
    footer {
        transform: translate(calc(100vw - 62px));
    }

    header {
        pointer-events: all;
    }
}

button {
    outline: none;
    border: 0;
    padding: 0;
}

.button {
    display: inline-block;
    width: auto;
    height: 32px;
    text-align: center;
    padding: 0 20px;
    box-sizing: border-box;
    background: #fd4954;
    border-radius: 16px;
    font-size: 18px;
    line-height: 32px;
    color: #ffffff;
    transition: background-color 0.3s;

    &:hover {
        color: #ffffff;
        background-color: #fc747d;
    }

    &:active {
        color: #ffffff;
        background-color: #c43741;
    }
}
