.house-frame {
    position: relative;
    width: 100%;
    padding-top: 60%;
    margin-top: -20px;
    margin-bottom: 10px;
    max-height: calc(100vh - 72px);

    &.hover {
        cursor: pointer;
    }

    canvas {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
    }

    &:after {
        content: "Loading";
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        transition: opacity 0.5s;
        opacity: 0;
        background-color: rgb(255, 255, 255);
        background-image: url("../../img/svg/loader.svg");
        background-repeat: no-repeat;
        background-position: center;
        color: #fd4954;
        pointer-events: none;
    }

    &.loading:after {
        opacity: 1;
    }
}

@media screen and (min-width: 1140px) {
    .house-frame {
        width: 100%;
        height: 100vh;
        padding-top: 0;
    }
}
