@import "../helpers/variables";

.round-button {
    width: 32px;
    height: 32px;
    border-radius: 16px;
    background-color: $sub_color;
    cursor: pointer;
    position: relative;
    font-size: 0;

    &:hover {
        filter: brightness(1.05);
    }

    &:active {
        filter: brightness(0.95);
    }
}
