@import "../helpers/variables";

header {
    box-sizing: border-box;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    min-width: 320px;
    height: 72px;
    padding: 0 4px;
    box-shadow: 0 5px 5px -5px rgba(0, 0, 0, 0.2);
    background-color: #fff;

    .container {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 100%;
    }

    .round-button {
        flex-shrink: 0;
    }
}

.logo {
    flex-shrink: 0;
    width: 180px;
    height: 19px;
    img {
        display: block;
        width: 100%;
    }
}

.menu-button {
    position: relative;
    width: 32px;
    height: 32px;
    cursor: pointer;
}

.menu-button__bar {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 26px;
    height: 2px;
    margin: -1px 0 0 -13px;
    background-color: $main_color;
    transform-origin: center;
    opacity: 1;

    transition: all 0.3s;

    &:nth-child(1) {
        margin-top: -9px;
    }

    &:nth-child(3) {
        margin-top: 7px;
    }
}

.side-open {
    .menu-button {
        &:before {
            content: "";
            display: block;
            position: absolute;
            top: -20px;
            left: -19px;
            width: 62px;
            height: 100vh;
        }
    }
    .menu-button__bar {
        &:nth-child(1) {
            margin-top: 0;
            transform: rotate(-45deg);
        }
        &:nth-child(2) {
            opacity: 0;
        }
        &:nth-child(3) {
            margin-top: 0;
            transform: rotate(45deg);
        }
    }
}

@media screen and (max-width: 1140px) {
    .header-menu {
        box-sizing: border-box;
        position: absolute;
        top: 0;
        right: 100%;
        width: calc(100vw - 62px);
        height: 100vh;
        padding: 15px;
        background-color: $main_color;

        ul {
            padding: 0;
            list-style-type: none;
        }

        a {
            display: block;
            height: 48px;
            line-height: 48px;
            color: #fff;
            &.active {
                color: #fff;
                font-weight: 500;
            }
        }
    }
}

@media screen and (min-width: 1140px) {
    .menu-button {
        display: none;
    }
    .header-menu {
        box-sizing: border-box;
        width: 100%;
        max-width: 780px;
        padding: 0 20px;
        margin-right: auto;
        ul {
            display: flex;
            align-items: center;
            justify-content: space-between;
            list-style-type: none;
            li {
                a.active {
                    color: $main_color;
                }
            }
        }
    }
}
