[class*="icn_"] {
    &:before {
        content: "";
        display: inline-block;
        width: 16px;
        height: 16px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;

        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }

    &.icn_6 {
        &:before {
            width: 6px;
            height: 6px;
        }
    }
}

@mixin icn($icn_name) {
    .icn_#{$icn_name} {
        &:before {
            @include bkimg2x(
                "../../img/ui/icn__#{$icn_name}.png",
                "../../img/ui/icn__#{$icn_name}@2x.png"
            );
        }
    }
}

@include icn("phone");
@include icn("point");
@include icn("letter");
@include icn("arrow");
@include icn("arrow_gray");
