.gallery-list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -2%;
}

.gallery-item {
    position: relative;
    width: 96%;
    height: 255px;
    margin: 2%;
}

.gallery-item__img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 48px;
    left: 0;
    width: 100%;
    overflow: hidden;
    background-color: #f9f9f9;
    background-image: url("../../img/svg/no-image.svg");
    background-position: center;
    background-repeat: no-repeat;

    img {
        display: block;
        width: 100%;
        height: 100%;
        transform: scale(1);
        transition: transform 0.3s;
        object-fit: cover;
        pointer-events: none;
    }
}

.gallery-item__label {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 48px;
    padding: 0 15px;
    transition: background-color 0.3s;
    background: #fd4954;
    color: #fff;
    font-size: 18px;
    line-height: 48px;
}

.gallery-item a {
    &:hover {
        img {
            transform: scale(1.2);
        }
        .gallery-item__label {
            background-color: #fd1f2e;
        }
    }
    &:active {
        img {
            transform: scale(1.1);
        }
    }
}

@media screen and (min-width: 580px) {
    .gallery-item {
        width: 48%;
        margin: 1%;
    }
}

@media screen and (min-width: 890px) {
    .gallery-item {
        width: 31%;
    }
}

@media screen and (min-width: 1140px) {
    .gallery-list {
        margin: 0 -1.5%;
    }
    .gallery-item {
        width: 30.333333%;
        margin: 1.5%;
    }
}
