@import "../helpers/variables";

footer {
    box-shadow: 0 -1px 0 #e9e9e9;
    width: 100%;
    padding: 15px 0 26px;
    margin-top: 30px;

    .container {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

.footer-menu {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin-top: 15px;
}

.footer-menu__col {
    width: 50%;

    a {
        display: block;
        height: 37px;
        line-height: 37px;
    }
}

.footer-contacts {
    width: 100%;
    flex-shrink: 0;
}

.footer-contacts__contact {
    color: $main_color;
    padding-left: 37px;
    position: relative;
    height: 26px;
    line-height: 26px;
    margin-top: 14px;

    span {
        display: block;
        position: absolute;
        top: 50%;
        left: 0;
        width: 26px;
        height: 26px;
        background-color: $sub_color;
        margin-top: -13px;
        border-radius: 13px;
    }
}

@media screen and (min-width: 1140px) {
    footer {
        .container {
            flex-direction: row;
            align-items: flex-start;
            justify-content: space-between;
        }

        .logo {
            margin-top: 13px;
        }
    }

    .footer-menu {
        margin-top: 0;
        padding: 0 20px;
        box-sizing: border-box;
    }

    .footer-menu__col {
        width: 25%;
    }

    .footer-contacts {
        width: 255px;
    }

    .footer-contacts__contact:first-child {
        margin-top: 5px;
    }
}
