.gallery-pictures {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -1%;
}

.gallery-picture {
    position: relative;
    width: 48%;
    height: 126px;
    margin: 1%;
}

.gallery-picture__img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    background-color: #f9f9f9;
    background-image: url("../../img/svg/no-image.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 50%;

    img {
        display: block;
        width: 100%;
        height: 100%;
        transform: scale(1);
        transition: transform 0.3s;
        object-fit: cover;
        pointer-events: none;
    }
}

.gallery-picture a {
    &:hover {
        img {
            transform: scale(1.2);
        }
    }
    &:active {
        img {
            transform: scale(1.1);
        }
    }
}

.gallery-pictures__more {
    display: flex;
    justify-content: center;
    width: 100%;
    margin-top: 16px;
}

.popup {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    z-index: 1;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}

.popup__blend {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(#000, 0.8);
    cursor: pointer;
}

.popup__content {
    display: flex;
    position: relative;
    min-width: 128px;
    max-width: 95%;
    min-height: 128px;
    max-height: 95%;

    img {
        display: block;
        max-width: 100%;
        max-height: 100%;
        object-fit: contain;
    }
}

.popup__arrows {
    display: flex;
    justify-content: space-between;
    position: absolute;
    top: 50%;
    right: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    pointer-events: none;
    width: 100vw;
}

.popup-arrow {
    position: relative;
    width: 64px;
    height: 64px;
    pointer-events: all;
    cursor: pointer;
    color: #fff;
    background: rgba(#000, .15);

    &:before {
        content: "";
        box-sizing: border-box;
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        width: 50%;
        height: 50%;

        border-left: 3px solid;
        border-top: 3px solid;
        transform-origin: 50% 50%;
    }

    &.prev {
        &:before {
            transform: rotate(-45deg) translate(20%, -50%);
        }
    }
    &.next {
        &:before {
            transform: rotate(135deg) translate(20%, 90%);
        }
    }

    &:hover{
        background: rgba(#fff, .05);
    }

    &:active{
        background: rgba(#ccc, .05);
    }
}

@media screen and (min-width: 580px) {
    .gallery-picture {
        width: 31.33333%;
    }
}

@media screen and (min-width: 890px) {
    .gallery-picture {
        width: 23%;
    }
}

@media screen and (min-width: 1140px) {
    .gallery-pictures {
        margin: 0 -0.3%;
    }
    .gallery-picture {
        width: 19%;
        margin: 0.3%;
    }
}
