@import "../helpers/variables";

.breadcrumbs {
    ul {
        list-style-type: none;
        font-size: 0;
        li {
            display: inline-block;
            position: relative;
            font-size: 12px;

            a,
            span {
                color: #ccc;
            }

            & + li {
                & > a,
                & > span {
                    padding-left: 16px;

                    &:before {
                        left: 5px;
                        transform: translate(0, -50%) rotate(90deg);
                    }
                }
            }

            &:last-child {
                a,
                span {
                    color: $main_color;
                }
            }
        }
    }
}
