@import "../helpers/variables";

.tabs {
}
ul.tabs__buttons {
    display: flex;
    height: 42px;
    margin: 8px 0 10px;
    padding: 0;
    list-style-type: none;
    overflow-x: auto;
    overflow-y: hidden;

    li {
        width: 100%;
        height: 42px;
        margin: 0 1px;
        text-align: center;
        font-size: 10px;
        line-height: 14px;

        &:before {
            display: none;
        }

        a {
            box-sizing: border-box;
            display: flex;
            align-items: center;
            justify-content: center;
            height: 42px;
            padding: 0 12px;
            color: #c2c6ce;
            border-bottom: 1px solid;
        }

        &.active {
            a {
                color: $main_color;
            }
        }
    }
}

.tabs__content {
    display: none;

    &.active {
        display: block;
    }
}

@media screen and (min-width: 1140px) {
    ul.tabs__buttons {
        overflow-x: hidden;
        li {
            font-size: 14px;
        }
    }
}
