@import "../helpers/variables";

.contacts-page {
    margin-top: 15px;
    display: flex;
    flex-wrap: wrap;
}

.contacts-page__contact {
    .h2 {
        color: $main_color;
    }

    p {
        margin-top: 0;
    }
}

.contacts-page__contacts {
    width: 100%;
}

.contacts-page__map {
    padding-top: 60%;
    width: 100%;
}

.contacts-page__pano {
    width: 100%;
    margin-top: 15px;
    padding-top: 60%;
}

.contacts-page__map,
.contacts-page__pano {
    position: relative;
    & > div {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
    }

    iframe {
        width: 100% !important;
        height: 100% !important;
    }
}

@media screen and (min-width: 1140px) {
    .contacts-page__contacts {
        width: 34%;
    }

    .contacts-page__map {
        padding-top: 40%;
        width: 66%;
    }

    .contacts-page__pano {
        width: 100%;
        padding-top: 43%;
    }
}
